import request from '@/utils/request'
export function list(data) {
    return request({
        url: 'admin/',
        method: 'get',
        params: data
    })
}
export function del(data) {
    return request({
        url: 'admin/delete',
        method: 'get',
        params: data
    })
}
export function update(data) {
    return request({
        url: 'admin/update',
        method: 'post',
        data
    })
}
export function add(data) {
    return request({
        url: 'admin/add',
        method: 'post',
        data
    })
}


