<template>
  <div class="index">
    <el-button type="primary" style="margin-bottom: 10px" @click="add"
      >新增</el-button
    >
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column type="index" width="50"> </el-table-column>
      <el-table-column prop="model_number" label="手机型号" width="180">
      </el-table-column>
      <el-table-column prop="is_usable" label="状态" width="180">
        <template slot-scope="scope">
          {{ scope.row.is_usable ? "可用" : "不可用" }}
        </template>
      </el-table-column>
      <el-table-column prop="version" label="系统版本"> </el-table-column>
      <el-table-column prop="sn_number" label="SN"> </el-table-column
      ><el-table-column fixed="right" label="操作" width="120">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="edit(scope.row)">
            修改
          </el-button>
          <el-button type="text" size="small" @click="del(scope.row.id)">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @current-change="currentChange"
        background
        layout="prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog title="修改" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="手机型号" :label-width="formLabelWidth">
          <el-input v-model="form.model_number" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="状态" :label-width="formLabelWidth">
          <!-- <el-input v-model="form.is_usable" autocomplete="off"></el-input> -->
          <el-switch v-model="form.is_usable" active-color="#13ce66">
          </el-switch>
        </el-form-item>
        <el-form-item label="系统版本" :label-width="formLabelWidth">
          <el-input v-model="form.version" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="SN" :label-width="formLabelWidth">
          <el-input v-model="form.sn_number" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="editSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { list, add, update, del } from "@/api";
export default {
  data() {
    return {
      form: {
        model_number: "",
        is_usable: "",
        version: "",
        sn_number: "",
      },
      formLabelWidth: "120px",
      dialogFormVisible: false,
      tableData: [],
      page: 1,
      size: 10,
      total: 1000,
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    currentChange(index) {
      this.page = index;
      this.getList();
    },
    add() {
      add().then((res) => {
        this.$message({
          type: "success",
          message: "新增成功!",
        });
        this.getList();
      });
    },
    getList() {
      let page = this.page;
      let size = this.size;
      list({ page, size }).then((res) => {
        this.tableData = res.data_list;
        this.total = res.total;
      });
    },
    del(mobileuser_id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          del({ mobileuser_id }).then((res) => {
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            this.getList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    edit(row) {
      this.form = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    editSubmit() {
      let data = JSON.parse(JSON.stringify(this.form));
      data.is_usable = data.is_usable ? 1 : 0;
      delete data.create_time;
      update(data).then((res) => {
        this.dialogFormVisible = false;
        this.$message({
          type: "success",
          message: "修改成功!",
        });
        this.getList();
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pagination {
  margin-top: 20px;
  text-align: center;
}
</style>



