import axios from 'axios'
import { Message, Loading } from 'element-ui';
import _ from 'lodash';
import Vue from 'vue'
let loadingInstance; //loading 实例
let needLoadingRequestCount = 0; //当前正在请求的数量

function showLoading() {
    let main = document.querySelector('body')
    if (main) {
        if (needLoadingRequestCount === 0 && !loadingInstance) {
            loadingInstance = Loading.service({
                target: main, text: '拼命加载中...', background: 'rgba(0,0,0,0.3)'
            });
        }
        needLoadingRequestCount++;
    }
}
function closeLoading() {
    Vue.nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
        needLoadingRequestCount--;
        needLoadingRequestCount = Math.max(needLoadingRequestCount, 0); // 保证大于等于0
        if (needLoadingRequestCount === 0) {
            if (loadingInstance) {
                hideLoading()
            }
        }
    });
}
//防抖：将 300ms 间隔内的关闭 loading 便合并为一次。防止连续请求时， loading闪烁的问题。
//因为有时会碰到在一次请求完毕后又立刻又发起一个新的请求的情况（比如删除一个表格一行后立刻进行刷新）
//这种情况会造成连续 loading 两次，并且中间有一次极短的闪烁。通过防抖可以让 300ms 间隔内的 loading 便合并为一次，避免闪烁的情况。
var hideLoading = _.debounce(() => {
    loadingInstance.close();
    loadingInstance = null;
}, 300);
const service = axios.create({
    // baseURL: "http://weebot.wisight.cn:28099/",
    baseURL: "http://localhost:5001/",
    timeout: 20000 // 过期时间
})

// request interceptor
service.interceptors.request.use(
    config => {
        // 发送请求之前做的
        if (/get/i.test(config.method)) {
            config.params = config.params || {}
            // config.params.temp = Date.parse(new Date()) / 1000
        }
        showLoading()
        return config
    },
    error => {
        closeLoading();
        return Promise.reject(error)
    }
)

// response interceptor
service.interceptors.response.use(
    /**
     * If you want to get http information such as headers or status
     * Please return  response => response
    */

    /**
     * Determine the request status by custom code
     * Here is just an example
     * You can also judge the status by HTTP Status Code
     */
    response => {
        closeLoading();
        const res = response.data
        console.log(res)
        // if the custom code is not 20000, it is judged as an error.
        if (res.status == 200) {
            return res
        } else {
            Message.error(res.errmsg);
            return Promise.reject()
        }
    },
    error => {
        closeLoading();
        Message.error('请求失败');
        return Promise.reject(error)
    }
)

export default service



